'use client';

import React, { useState, useEffect, useCallback } from 'react';
import { useAuth } from '@/context/AuthContext';
import { listingsApi } from '@/lib/listings';
import { requestsApi } from '@/lib/requests';
import { Listing } from '@shared/types';
import { Search, Filter, MapPin, Clock, Users, Heart, X } from 'lucide-react';

const NGOListingsPage: React.FC = () => {
  const { user } = useAuth();
  const [listings, setListings] = useState<Listing[]>([]);
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState('');
  const [foodTypeFilter, setFoodTypeFilter] = useState('all');
  const [claimingId, setClaimingId] = useState<string | null>(null);
  const [showRequestModal, setShowRequestModal] = useState(false);
  const [selectedListing, setSelectedListing] = useState<Listing | null>(null);
  const [reqAddress, setReqAddress] = useState('');
  const [reqUrgency, setReqUrgency] = useState<'low' | 'medium' | 'high'>('medium');
  const [reqQuantityAmount, setReqQuantityAmount] = useState<number>(0);
  const [submittingRequest, setSubmittingRequest] = useState(false);

  const fetchListings = useCallback(async () => {
    try {
      setLoading(true);
      // Don't filter by city/state initially - show all available food
      const response = await listingsApi.getAvailableListings(
        search,
        foodTypeFilter,
        1,
        10
        // Removed city/state filtering to show all available food
      );
      setListings(response.listings);
    } catch (error) {
      console.error('Error fetching listings:', error);
    } finally {
      setLoading(false);
    }
  }, [search, foodTypeFilter]);

  useEffect(() => {
    fetchListings();
  }, [fetchListings]);

  const handleClaim = async (id: string) => {
    if (window.confirm('Are you sure you want to claim this food listing?')) {
      try {
        setClaimingId(id);
        await listingsApi.claimListing(id);
        fetchListings(); // Refresh the list
        alert('Food listing claimed successfully!');
      } catch (error) {
        console.error('Error claiming listing:', error);
        alert('Failed to claim listing');
      } finally {
        setClaimingId(null);
      }
    }
  };

  const openRequestModal = (listing: Listing) => {
    setSelectedListing(listing);
    setReqAddress(listing.pickupLocation.address || '');
    setReqUrgency('medium');
    setReqQuantityAmount(listing.quantity.amount);
    setShowRequestModal(true);
  };

  const closeRequestModal = () => {
    setShowRequestModal(false);
    setSelectedListing(null);
  };

  const submitRequest = async () => {
    if (!selectedListing) return;
    try {
      setSubmittingRequest(true);
      const payload = {
        listingId: selectedListing._id,
        title: selectedListing.title,
        description: selectedListing.description,
        foodType: selectedListing.foodType,
        quantity: { amount: Number(reqQuantityAmount) || selectedListing.quantity.amount, unit: selectedListing.quantity.unit },
        preferredPickup: { address: reqAddress },
        urgencyLevel: reqUrgency,
      } as const;
      await requestsApi.create(payload);
      closeRequestModal();
      alert('Request sent to the hotel successfully. Track it under NGO > Requests.');
    } catch (e) {
      console.error('Error sending request', e);
      alert('Failed to send request');
    } finally {
      setSubmittingRequest(false);
    }
  };

  const isExpiringSoon = (expiryDate: string) => {
    const expiry = new Date(expiryDate);
    const now = new Date();
    const diffHours = (expiry.getTime() - now.getTime()) / (1000 * 3600);
    return diffHours <= 24; // Within 24 hours
  };

  const getTimeUntilExpiry = (expiryDate: string) => {
    const expiry = new Date(expiryDate);
    const now = new Date();
    const diffHours = Math.ceil((expiry.getTime() - now.getTime()) / (1000 * 3600));
    
    if (diffHours < 1) return 'Expires very soon';
    if (diffHours === 1) return '1 hour left';
    if (diffHours < 24) return `${diffHours} hours left`;
    
    const diffDays = Math.ceil(diffHours / 24);
    if (diffDays === 1) return '1 day left';
    return `${diffDays} days left`;
  };

  if (loading) {
    return (
      <div className="max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8">
        <div className="text-center">Loading available food...</div>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900">
          Available Food Listings
        </h1>
        <p className="mt-2 text-gray-600">
          Find food donations from hotels in your area
        </p>
      </div>

      {/* Search and Filters */}
      <div className="mb-6 space-y-4">
        <div className="flex flex-col sm:flex-row gap-4">
          <div className="relative flex-1">
            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
              <Search className="h-5 w-5 text-gray-400" />
            </div>
            <input
              type="text"
              placeholder="Search food listings..."
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              className="block w-full pl-10 pr-3 py-2 border border-gray-300 rounded-md leading-5 bg-white placeholder-gray-500 focus:outline-none focus:placeholder-gray-400 focus:ring-1 focus:ring-green-500 focus:border-green-500"
            />
          </div>
          
          <div className="flex items-center gap-2">
            <Filter className="h-5 w-5 text-gray-400" />
            <select
              value={foodTypeFilter}
              onChange={(e) => setFoodTypeFilter(e.target.value)}
              className="rounded-md border-gray-300 shadow-sm focus:border-green-500 focus:ring-green-500"
            >
              <option value="all">All Food Types</option>
              <option value="prepared">Prepared Food</option>
              <option value="raw">Raw Ingredients</option>
              <option value="packaged">Packaged Items</option>
              <option value="mixed">Mixed</option>
            </select>
          </div>
        </div>
      </div>

      {/* Listings */}
      {listings.length === 0 ? (
        <div className="text-center py-12">
          <Heart className="mx-auto h-12 w-12 text-gray-400" />
          <h3 className="mt-2 text-sm font-medium text-gray-900">No food available</h3>
          <p className="mt-1 text-sm text-gray-500">
            Check back later for new donations from hotels.
          </p>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {listings.map((listing) => (
            <div key={listing._id} className="bg-white rounded-lg shadow-md overflow-hidden hover:shadow-lg transition-shadow">
              <div className="p-6">
                <div className="flex items-start justify-between mb-3">
                  <h3 className="text-lg font-semibold text-gray-900">
                    {listing.title}
                  </h3>
                  {isExpiringSoon(listing.expiryDateTime) && (
                    <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-red-100 text-red-800">
                      <Clock className="h-3 w-3 mr-1" />
                      Urgent
                    </span>
                  )}
                </div>
                
                <p className="text-gray-600 text-sm mb-4">
                  {listing.description}
                </p>
                
                {/* Food Details */}
                <div className="space-y-3 mb-4">
                  <div className="flex items-center text-sm">
                    <Users className="h-4 w-4 text-gray-500 mr-2" />
                    <span className="font-medium text-gray-900">
                      {listing.quantity.amount} {listing.quantity.unit}
                    </span>
                    <span className="ml-2 px-2 py-0.5 bg-gray-100 text-gray-700 rounded text-xs capitalize">
                      {listing.foodType}
                    </span>
                  </div>
                  
                  <div className="flex items-center text-sm">
                    <Clock className="h-4 w-4 text-gray-500 mr-2" />
                    <span className={`${isExpiringSoon(listing.expiryDateTime) ? 'text-red-600 font-medium' : 'text-gray-700'}`}>
                      {getTimeUntilExpiry(listing.expiryDateTime)}
                    </span>
                  </div>
                  
                  <div className="flex items-start text-sm">
                    <MapPin className="h-4 w-4 text-gray-500 mr-2 mt-0.5" />
                    <div>
                      <p className="text-gray-700 font-medium">{listing.hotel.organizationName}</p>
                      <p className="text-gray-500">{listing.pickupLocation.address}</p>
                    </div>
                  </div>
                </div>
                
                {/* Allergens */}
                {listing.allergens.length > 0 && (
                  <div className="mb-4">
                    <p className="text-sm font-medium text-gray-700 mb-1">Contains allergens:</p>
                    <div className="flex flex-wrap gap-1">
                      {listing.allergens.map((allergen: string) => (
                        <span
                          key={allergen}
                          className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-yellow-100 text-yellow-800"
                        >
                          {allergen}
                        </span>
                      ))}
                    </div>
                  </div>
                )}
                
                {/* Special Instructions */}
                {listing.specialInstructions && (
                  <div className="mb-4">
                    <p className="text-sm font-medium text-gray-700">Special Instructions:</p>
                    <p className="text-sm text-gray-600 mt-1">{listing.specialInstructions}</p>
                  </div>
                )}
                
                {/* Claim Button */}
                <div className="grid grid-cols-1 sm:grid-cols-2 gap-2">
                  <button
                    onClick={() => handleClaim(listing._id)}
                    disabled={claimingId === listing._id}
                    className="w-full flex items-center justify-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    {claimingId === listing._id ? (
                      'Claiming...'
                    ) : (
                      <>
                        <Heart className="h-4 w-4 mr-2" />
                        Claim
                      </>
                    )}
                  </button>
                  <button
                    onClick={() => openRequestModal(listing)}
                    className="w-full flex items-center justify-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500"
                  >
                    Send Request
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>
      )}
      {/* Request Modal */}
      {showRequestModal && selectedListing && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 p-4">
          <div className="bg-white rounded-lg shadow-xl max-w-lg w-full">
            <div className="flex items-center justify-between px-4 py-3 border-b">
              <h3 className="text-lg font-semibold">Send Request</h3>
              <button onClick={closeRequestModal} className="text-gray-500 hover:text-gray-700">
                <X className="h-5 w-5" />
              </button>
            </div>
            <div className="p-4 space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700">Pickup Address</label>
                <input
                  type="text"
                  value={reqAddress}
                  onChange={(e) => setReqAddress(e.target.value)}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-green-500 focus:ring-green-500"
                />
              </div>
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700">Urgency</label>
                  <select
                    value={reqUrgency}
                    onChange={(e) => setReqUrgency(e.target.value as 'low' | 'medium' | 'high')}
                    className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-green-500 focus:ring-green-500"
                  >
                    <option value="low">Low</option>
                    <option value="medium">Medium</option>
                    <option value="high">High</option>
                  </select>
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700">Quantity</label>
                  <div className="mt-1 flex">
                    <input
                      type="number"
                      min={1}
                      value={reqQuantityAmount}
                      onChange={(e) => setReqQuantityAmount(Number(e.target.value))}
                      className="block w-full rounded-l-md border-gray-300 shadow-sm focus:border-green-500 focus:ring-green-500"
                    />
                    <span className="inline-flex items-center px-3 rounded-r-md border border-l-0 border-gray-300 bg-gray-50 text-gray-500 text-sm">
                      {selectedListing.quantity.unit}
                    </span>
                  </div>
                </div>
              </div>
            </div>
            <div className="px-4 py-3 border-t flex justify-end gap-2">
              <button
                onClick={closeRequestModal}
                className="px-4 py-2 rounded-md text-sm font-medium text-gray-700 bg-white border border-gray-300 hover:bg-gray-50"
              >
                Cancel
              </button>
              <button
                disabled={submittingRequest}
                onClick={submitRequest}
                className="px-4 py-2 rounded-md text-sm font-medium text-white bg-green-600 hover:bg-green-700 disabled:opacity-50"
              >
                {submittingRequest ? 'Sending...' : 'Send Request'}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default NGOListingsPage;